import React, { Fragment, useMemo, useState } from "react";
import { useWeb3React, UnsupportedChainIdError } from "@web3-react/core";
// import { AbstractConnector } from "@web3-react/abstract-connector";
import { shortenAddress } from "../../../../utils";
import Identicon from "../../../Identicon";
import Icon from "../../../Icon";
// import { injected } from "../../../../connectors";
import PendingLoader from "../../../PendingLoader";
import cn from "classnames";
import {
  isTransactionRecent,
  useAllTransactions,
  newTransactionsFirst,
} from "../../../../state/transactions/hooks";
import "./ConnectWallet.scss";
// import { useView } from "../../../../state/view/hooks";
import { isMobile } from "react-device-detect";
import WalletModal from "../WalletModal";

import AccountModal from "../AccountModal";

const ConnectWallet: React.FC<{
  toggleModal?: () => void;
  theme?: "dark" | "light";
}> = ({ theme = "dark" }) => {
  const [showWalletModal, setWalletModal] = useState<boolean>();
  const [showAccountModal, setAccountModal] = useState(false);
  const { account, error } = useWeb3React();

  const allTransactions = useAllTransactions();
  const sortedRecentTransactions = useMemo(() => {
    const txs = Object.values(allTransactions);
    return txs.filter(isTransactionRecent).sort(newTransactionsFirst);
  }, [allTransactions]);

  const pending = sortedRecentTransactions
    .filter((tx) => !tx.receipt)
    .map((tx) => tx.hash);

  const hasPendingTransactions = !!pending.length;

  const toggleAccoutModal = () => {
    setAccountModal(!showAccountModal);
  };

  const WalletDOM = useMemo(() => {
    if (account) {
      return (
        <div className="account-info" onClick={toggleAccoutModal}>
          {hasPendingTransactions ? (
            <div className="addr-pding">
              <span className="pding">{`${pending?.length} Pending`}</span>
              <PendingLoader size="18" />
            </div>
          ) : (
            <span className="addr">{shortenAddress(account)}</span>
          )}
          <Identicon />
        </div>
      );
    }
    if (error) {
      return (
        <Fragment>
          <Icon symbol="lighnting" size={16} />
          <button className="error">
            {error instanceof UnsupportedChainIdError
              ? "Wrong Network"
              : "Error"}
          </button>
        </Fragment>
      );
    }
    return (
      <button
        className="connect"
        onClick={() => {
          setWalletModal(!showWalletModal);
        }}
      >
        <span>Connect a wallet</span>
        <Icon symbol={theme === "light" ? "wallet2" : "wallet"} />
      </button>
    );
  }, [account, error, hasPendingTransactions, pending?.length]);

  return (
    <>
      <div
        className={cn("wallet", {
          Errormsg: error,
          Unconnect: !account && !error,
        })}
      >
        {WalletDOM}
      </div>
      <AccountModal
        isOpen={showAccountModal}
        toggleAccountModal={toggleAccoutModal}
      />
      {showWalletModal && !isMobile && (
        <WalletModal
          toggleWalletModal={() => {
            setWalletModal(!showWalletModal);
          }}
        />
      )}
    </>
  );
};

export default ConnectWallet;
