import React from 'react'
import './index.scss'
// import gLoader1 from '../../assets/images/gLoader.gif'
// import gLoader2 from '../../assets/images/gLoader_2.gif'
// import gLoader3 from '../../assets/images/loader_2.gif'
import img from '../../assets/images/loading.png'
import {useView} from "../../state/view/hooks";

// import loader from '../../assets/images/snapshot.svg'

type Props = {
  size?: number
}

const  Loader: React.FC<Props> = ({size}) => {
  const {isMobile} = useView()
  let _size = size || (isMobile ? 40 : 90)
  return (
    <div className="gLoader" style={{height: `${_size}px`, width: `${_size}px`}}>
      <img src={img} alt='' />
    </div>
  )
}

export default Loader
