import React, {useCallback, useEffect, useRef, useState} from 'react'
import './index.scss'
import {init, dispose, KLineData} from 'klinecharts'
// import generatedKLineDataList from './generatedKLineDataList'
import {MaskLoading, ResizableElement} from '../'
import TopBar from "./TopBar";
import SideBar from "./SideBar";
import { useView } from "../../state/view/hooks";
// import { useGlobal } from "../../state/global/hooks";
import { useRequest } from "ahooks";
import { getKlineData } from "../../service";
import {useList} from "../../state/list/hooks";
import ZoomOut from "./assets/zoom_out.png"
import {CurrentPairs} from "../../state/list/reducer";
// import Loader from "../Loader";
// import BottomBar from "./BottomBar";

export enum Periods { "1m" = "1m", "5m" = "5m", "30m" = "30m", "1h" = "1h", "6h" = "6h", "12h" = "12h", "1d" = "1d" }
const periodInterval: {[props in Periods]: number} = {
  "1m": 60,
  "5m": 60 * 5,
  "30m": 60 * 30,
  "1h": 60 * 60,
  "6h": 60 * 60 * 6,
  "12h": 60 * 60 * 12,
  "1d": 60 * 60 * 24,
}

function enumValues<T extends object>(enumeration: T): Array<T[keyof T]> {
  return Object
    .keys(enumeration)
    .filter(k => isNaN(Number(k)))
    .map(k => enumeration[k as keyof T]);
}

export const periods = enumValues(Periods)

const Kline: React.FC = () => {
  const [period, setPeriod] = useState(Periods['1m'])
  const { CurrentSwapLiquidity: pairs, OtherToken, /*TargetToken*/ } = useList();
  const { data: klineData, run: klineRun, loading: klineLoading, mutate: klineMutate } = useRequest(getKlineData, {
    manual: true,
    initialData: null,
    debounceInterval: 10,
    formatResult: (data: Result) => {
      if (data?.success) return data.data
      return null
    },
  })

  // console.log('CurrentSwapLiquidity---')
  // console.log(pairs, TargetToken, OtherToken)
  const { isMobile } = useView()
  const [isFullscreen, setIsFullscreen] = useState(false)
  const box = useRef<HTMLElement | null>(null)
  const boxHeight = useRef(0)
  const kLineChart = useRef<any>(null)
  const lastLoad = useRef<{
    pairs: Pairs | null
    period: Periods | null
  }>({ pairs: null, period: null })

  useEffect(() => {
    if (pairs) {
      lastLoad.current = { pairs, period }
      let now = new Date().getTime() / 1000
      let nowTime = now - now%periodInterval[period]
      klineMutate(null)
      kLineChart.current?.clearData()
      klineRun(pairs.address, period, pairs.type === 0 ? 1 : 0, nowTime - periodInterval[period] * 100, nowTime)
      // klineRun(pairs.address, period, pairs.type === 0 ? 1 : 0)
    }
  }, [klineRun, period, pairs, klineMutate])

  useEffect(() => {
    function getData (endData?: KLineData) {
      const _data = ([...klineData] || []) as [string, string, string, string, string, string, number][];
      function addLastData () {
        const {open, close, high, low, volume, turnover, timestamp} = endData as {
          open: number, close: number, high: number, low: number, volume: number, turnover: number, timestamp: number}
        _data.push([open + '', close+ '', high+ '', low+ '', volume+ '', turnover+ '', timestamp])
      }
      // if(endData) console.log(0, endData)
      if(endData) addLastData()
      // if(endData) console.log(1, _data)
      const decimal = OtherToken?.decimal as number
      // console.log('decimal---')
      // console.log(decimal)
      const fixed = Math.pow(10, decimal)
      let s = periodInterval[period]
      let t = s * 1000
      let data: KLineData[] = [];
      for (let i=0; i<_data.length; i++){
        const [open, close, high, low, volume, turnover, timestamp] = _data[i];
        const lastData = data[data.length - 1]
        const _t = (timestamp - lastData?.timestamp) / t
        if (lastData && _t > 1) {
          for(let j=0; j<_t-1; j++) {
            data.push({
              open: lastData.close,
              close: lastData.close,
              high: lastData.close,
              low: lastData.close,
              volume: 0,
              turnover: 0,
              timestamp: lastData.timestamp + t*(j+1)
            })
          }
        }
        data.push({
          open: Number(open) / fixed,
          close: Number(close) / fixed,
          high: Number(high) / fixed,
          low: Number(low) / fixed,
          volume: Number(volume),
          turnover: Number(turnover) / fixed,
          timestamp
        })
      }

      if(endData) data.pop()

      return data
    }
    if (klineData?.length) {

      // console.log('data----')
      // console.log(data)
      if(kLineChart.current){
        let lastData = kLineChart.current.getDataList()
        if ((lastData || []).length) {
          kLineChart.current.applyMoreData(getData(lastData[0]))
        } else {
          kLineChart.current.applyNewData(getData())
        }
      }
    }
  }, [OtherToken?.decimal, klineData, period])

  const resize = () => {
    // console.log('resize')
    kLineChart.current && kLineChart.current.resize()
  }

  useEffect(() => {
    box.current = document.getElementById('kline-wrapper')
    kLineChart.current = init('kline', isMobile ? {
      yAxis: { inside: true, },
      candle: {
        tooltip: {
          showType: 'rect',
          labels: ['Time', 'Open', 'Close', 'High', 'Low', 'Vol'],
          rect: {
            paddingLeft: 0,
            paddingRight: 0,
            paddingTop: 0,
            paddingBottom: 6,
            offsetLeft: 4,
            offsetTop: 4,
            borderRadius: 3,
            borderSize: 0,
            borderColor: 'rgba(255, 255, 255, 0.5)',
            fillColor: '#171C24',
          },
        }
      }
    } : {
      candle: {
        tooltip: {
          labels: ['Time', 'Open', 'Close', 'High', 'Low', 'Vol'],
        }
      }
    })
    kLineChart.current.setDataSpace(isMobile ? 15 : 25)
    kLineChart.current.createTechnicalIndicator('VOL', false, { height: 50 })
    window.addEventListener('resize', resize)

    return () => {
      dispose('kline')
      kLineChart.current = null
      window.removeEventListener('resize', resize)
    }
  }, [isMobile])

  const loadMore = useCallback(() => {
    // console.log('loadMore---')
    if (!klineLoading && lastLoad.current) {
      // console.log('loadMore+++')
      // console.log()
      const lastStartTime = (kLineChart.current.getDataList() || [{timestamp: 0}])[0].timestamp / 1000
      if (lastStartTime) {
        const { pairs , period } = lastLoad.current as {pairs: CurrentPairs, period: Periods}
        klineRun(pairs.address, period, pairs.type === 0 ? 1 : 0, lastStartTime - periodInterval[period] * 50, lastStartTime)
      }
    }
  }, [klineLoading, klineRun])

  useEffect(() => {
    if (kLineChart.current) {
      kLineChart.current.loadMore(loadMore)
    }
  }, [loadMore])

  const onFullScreen = () => {
    setIsFullscreen(true)
    if (box.current) {
      boxHeight.current = box.current.clientHeight
    }

    setTimeout(resize, 60)
  }

  const exitFullscreen = () => {
    setIsFullscreen(false)
    if (box.current) {
      box.current.style.height = boxHeight.current + 'px'
    }
    setTimeout(resize, 60)
  }

  return (
    <div className={`kline-wrapper${isFullscreen ? ' fullscreen landscape' : ''}`}>
      <TopBar kLineChart={kLineChart} onFullScreen={onFullScreen} period={period} setPeriod={setPeriod} loading={klineLoading} />
      <ResizableElement className='kline-center' id='kline-wrapper' throttle={50} onResize={resize}>
        <SideBar kLineChart={kLineChart} />
        <div id="kline" className="kline-chart" />
      </ResizableElement>
      {/*<BottomBar />*/}
      {isFullscreen && <button onClick={exitFullscreen} className={'exitFullScreen'}><img src={ZoomOut} alt=""/></button>}
      {(klineLoading && !klineData)  && <MaskLoading />}
    </div>
  )
}
export default Kline;
