import { useState, useEffect } from "react";
import Icon from "../../../Icon";
import "./index.scss";
import { useView } from "../../../../state/view/hooks";
import { SUPPORTED_WALLETS } from "../../../../constants";
import { fortmatic, injected, portis } from "../../../../connectors";
import { AbstractConnector } from "@web3-react/abstract-connector";
import { UnsupportedChainIdError, useWeb3React } from "@web3-react/core";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import cn from "classnames";

const WALLET_VIEWS = {
  OPTIONS: "options",
  OPTIONS_SECONDARY: "options_secondary",
  ACCOUNT: "account",
  PENDING: "pending",
};

const Option = ({
  icon,
  active,
  onClick,
  header,
  link,
  walletView,
  pendingError,
}: {
  icon: string | undefined | null;
  active?: boolean;
  header: React.ReactNode;
  link?: string | undefined | null;
  walletView?: string;
  pendingError?: boolean;
  onClick?: () => void;
}) => {
  return (
    <button
      className={cn("option", { active: active, error: pendingError })}
      onClick={() => {
        if (!!link) {
          window.open(link, "_blank");
        } else {
          onClick && onClick();
        }
      }}
    >
      <div className="header">
        <Icon iconSrc={icon ?? ""} />
        <span>
          {pendingError
            ? "Conected fail"
            : !active && walletView === WALLET_VIEWS.PENDING
            ? " Initializing..."
            : header}
        </span>
      </div>
      {active ? <Icon symbol="selected" /> : null}
    </button>
  );
};

const WalletModal = ({
  toggleWalletModal,
}: {
  toggleWalletModal: () => void;
}) => {
  const { isMobile } = useView();
  const { active, account, connector, activate, error } = useWeb3React();

  const [walletView, setWalletView] = useState(WALLET_VIEWS.ACCOUNT);

  const [pendingWallet, setPendingWallet] = useState<
    AbstractConnector | undefined
  >();

  const [pendingError, setPendingError] = useState<boolean>();

  const tryActivation = async (connector: AbstractConnector | undefined) => {
    let name = "";
    Object.keys(SUPPORTED_WALLETS).map((key) => {
      if (connector === SUPPORTED_WALLETS[key].connector) {
        return (name = SUPPORTED_WALLETS[key].name);
      }
      return true;
    });
    setPendingWallet(connector); // set wallet for pending view
    setWalletView(WALLET_VIEWS.PENDING);

    if (
      connector instanceof WalletConnectConnector &&
      connector.walletConnectProvider?.wc?.uri
    ) {
      connector.walletConnectProvider = undefined;
    }

    connector &&
      activate(connector, undefined, true).catch((error) => {
        if (error instanceof UnsupportedChainIdError) {
          activate(connector); // a little janky...can't use setError because the connector isn't set
        } else {
          setPendingError(true);
        }
      });
  };

  // close on connection, when logged out before
  useEffect(() => {
    if (account) {
      toggleWalletModal();
    }
  }, [account, toggleWalletModal]);

  const getOptions = () => {
    const isMetamask = window.ethereum && window.ethereum.isMetaMask;
    return Object.keys(SUPPORTED_WALLETS).map((key) => {
      const option = SUPPORTED_WALLETS[key];
      // check for mobile options
      if (isMobile) {
        //disable portis on mobile for now
        if (option.connector === portis) {
          return null;
        }

        if (!window.web3 && !window.ethereum && option.mobile) {
          const isClicked = pendingWallet === option.connector;
          return (
            <Option
              onClick={() => {
                option.connector !== connector &&
                  !option.href &&
                  tryActivation(option.connector);
              }}
              key={key}
              active={option.connector && option.connector === connector}
              link={option.href}
              header={option.name}
              icon={option.iconURL}
              pendingError={isClicked ? pendingError : false}
              walletView={isClicked ? walletView : WALLET_VIEWS.ACCOUNT}
            />
          );
        }
        return null;
      }

      // overwrite injected when needed
      if (option.connector === injected) {
        // don't show injected if there's no injected provider
        if (!(window.web3 || window.ethereum)) {
          if (option.name === "MetaMask") {
            const isClicked = pendingWallet === option.connector;
            return (
              <Option
                key={key}
                header={"Install Metamask"}
                link={"https://metamask.io/"}
                icon={option.iconURL}
                pendingError={isClicked ? pendingError : false}
                walletView={isClicked ? walletView : WALLET_VIEWS.ACCOUNT}
              />
            );
          } else {
            return null; //dont want to return install twice
          }
        }
        // don't return metamask if injected provider isn't metamask
        else if (option.name === "MetaMask" && !isMetamask) {
          return null;
        }
        // likewise for generic
        else if (option.name === "Injected" && isMetamask) {
          return null;
        }
      }

      // return rest of options
      const isClicked = pendingWallet === option.connector;
      return (
        !isMobile &&
        !option.mobileOnly && (
          <Option
            onClick={() => {
              option.connector === connector
                ? setWalletView(WALLET_VIEWS.ACCOUNT)
                : !option.href && tryActivation(option.connector);
            }}
            key={key}
            active={option.connector === connector}
            link={option.href}
            header={option.name}
            icon={option.iconURL}
            pendingError={isClicked ? pendingError : false}
            walletView={isClicked ? walletView : WALLET_VIEWS.ACCOUNT}
          />
        )
      );
    });
  };

  return (
    <div className="dats-mask">
      <div className="dats-mask__cont wallet-modal__cont">
        <button
          className="dats-mask__close"
          onClick={() => {
            toggleWalletModal();
            setPendingError(false);
            setWalletView(WALLET_VIEWS.ACCOUNT);
          }}
        />
        <div className="wallet-modal__title">Connect to a wallet</div>
        <div className="wallet-modal__list">{getOptions()}</div>
      </div>
    </div>
  );
};

export default WalletModal;
