import React, {useEffect, useMemo, useState} from "react";
import './index.scss'
import LineChart from "../LineChart";
import { useGlobal } from '../../../../state/global/hooks'
import cn from "classnames";
import {getLiquidityChartData, getTradeChartData} from "../../../../service";
import {useList} from "../../../../state/list/hooks";
import {useRequest} from "ahooks";
import {amount} from "../../../../utils";
import {Loader} from "../../../../components";

type keyType = 'liquidity' | 'trade'

interface Props {
  type: keyType,
}

const defaultRequestOptions = {
  manual: true,
  initialData: [],
  formatResult: (data: Result) => {
    if (data?.success) return data?.data.sort(([m]: [number], [n]: [number]) => m - n) || []
    return []
  },
}

function formatDate(millinSeconds: number){
  let date = new Date(millinSeconds);
  let monthArr = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Spt","Oct","Nov","Dec"];
  let suffix = ["st","nd","rd","th"];

  let year = date.getFullYear(); //年
  let toYear = new Date().getFullYear(); //今年
  let month = monthArr[date.getMonth()]; //月
  let ddate = date.getDate(); //日

  let _ddate = ''
  if(ddate % 10 < 1 || ddate % 10 > 3) {
    _ddate = ddate + suffix[3];
  }else if(ddate % 10 === 1) {
    _ddate = ddate + suffix[0];
  } else if(ddate % 10 === 2) {
    _ddate = ddate + suffix[1];
  }else {
    _ddate = ddate + suffix[2];
  }
  return _ddate + " "+ month + `${toYear === year ? '' : ' ' + year}`;
}

// type ChartRawDataLine = [number, string];
type ChartRawData = [number, string]
const filledData = (data : ChartRawData[] = [], isLiquidity: boolean, isAll: boolean): ChartRawData[] => {

  let now = new Date()
  let today = new Date(`${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()} 08:00:00`).getTime() / 1000
  let arrDays = []
  for(let i=0; i<7; i++){
    arrDays[i] = today - 60 * 60 * 24 * i
  }
  arrDays.reverse()

  if (data.length){
    if (isAll) {

      let tTime = arrDays[0] - data[0][0]
      // console.log(arrDays[0], data[0][0], tTime)
      if(tTime > 0) {
        let tDay = Math.floor(tTime / (60 * 60 * 24))
        for(let i=0; i<7+tDay; i++){
          arrDays[i] = today - 60 * 60 * 24 * i
        }
        arrDays.reverse()
      }
    }

    let firstDataIndex = arrDays.indexOf(data[0][0])

    if (firstDataIndex === -1) return arrDays.map((time) => [time, '0'])

    let objData: {[props: number]: string} = {}
    data.forEach(([d1, d2]) => {
      objData[d1] = d2
    })

    let _data:ChartRawData[] = [];
    for (let j=0; j<arrDays.length; j++){
      let time = arrDays[j];
      _data[j] = j < firstDataIndex
        ? [time, data[0][1]]
        : [time, objData[time] || (isLiquidity ?  _data[j - 1][1] : '0')]
    }

    // console.log('_data=====')
    // console.log(_data)

    return _data
  }

  return arrDays.map((time) => [time, '0'])
}
// console.log(filledData([[1619827200, "336962153"]], false))

const useChartData = (type: keyType, isAll: boolean) => {
  const {CurrentSwapLiquidity, OtherToken} = useList()
  const {data: LData, run: LRun, loading: LLoading, mutate: LMutate} = useRequest(getLiquidityChartData, defaultRequestOptions)
  const {data: TData, run: TRun, loading: RLoading, mutate: TMutate} = useRequest(getTradeChartData, defaultRequestOptions)
  useEffect(() => {
    if (CurrentSwapLiquidity) {
      const {address} = CurrentSwapLiquidity;
      type === 'liquidity'
       ? LRun(address, CurrentSwapLiquidity.type === 0 ? 1 : 0, isAll)
       : TRun(address, CurrentSwapLiquidity.type === 0 ? 1 : 0, isAll)
    }

  }, [CurrentSwapLiquidity, isAll, LRun, TRun, type])

  useEffect(() => {
    if (LData && LData.length > 0) {
      LMutate([])
    }

    if (TData && TData.length > 0) {
      TMutate([])
    }
  }, [CurrentSwapLiquidity])


  const _LData = useMemo(() => {
    let _data = filledData(LData, true, isAll)
    return _data.map(([d1, d2]: ChartRawData) => {
      return {date: formatDate(d1 * 1000), value: Number(amount(d2, OtherToken?.decimal || 0))}
    })
  }, [LData, isAll, OtherToken?.decimal])

  const _TData = useMemo(() => {
    let _data = filledData(TData, false, isAll)
    return _data.map(([d1, d2]: ChartRawData) => {
      return {date: formatDate(d1 * 1000), value: Number(amount(d2, OtherToken?.decimal || 0))}
    })
  }, [OtherToken?.decimal, TData, isAll])

  // console.log('_TData----')
  // console.log(_TData)

  return {data: type === 'liquidity' ? _LData : _TData , loading: type === 'liquidity' ? LLoading : RLoading}
}
const TradeHeader: React.FC<Props> = ({ type }) => {
  const { unitToken } = useGlobal()
  const [isAllTime, setIsAllTime] = useState(false)
  const {data, loading} = useChartData(type, isAllTime)
  const { title, color } = (() => {
    let title = ''
    let color = 'white'
    switch (type) {
      case 'liquidity':
        title = 'Liquidity'
        color = '#2273f4'
        break
      case 'trade':
        title = 'Trading Volume, 24h'
        color = '#7e73f4'
        break
    }

    return { title, color }
  })()

  let {vol, isUp, percent} = useMemo(() => {
    let d1 = data[data?.length - 1]?.value || 0
    let d2 = data[data?.length - 2]?.value || 0
    let n = d1 - d2
    let isUp = n >= 0
    let percent = d2 ? (n / (d2 * 100)).toFixed(8) : '0.00'
    return {vol: n.toFixed(4), isUp, percent}
  }, [data])
  return (
    <div className='c-trade-header'>
      {/*<p className='title'>{title}</p>*/}
      <div className='amount'>
        <p>
          {vol}
          {/*{isETH && <i style={{ fontSize: '24px' }}>ETH</i>}*/}
        </p>
        {isUp ? <p className='up'>↑{percent}%</p> : <p className='down'>↓{percent}%</p>}

      </div>
      <div className='btn-row'>
        <div className='tab-btns'>
          <button className={cn('tab-button', { 'active': !isAllTime })} onClick={() => { setIsAllTime(false) }}>1 Week</button>
          <button className={cn('tab-button', { 'active': isAllTime })} onClick={() => { setIsAllTime(true) }}>All Time</button>
        </div>
        {loading && <Loader size={20} />}
      </div>

      <LineChart color={color} data={data} keys={['date', 'value']} />
    </div>
  )
}

export default TradeHeader;
