import React, { Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import Token from "./pages/Token";
import MobileToken from "./pages/mobile/Token";
import Home from "./pages/Home";
import {Web3ReactManager, AppHeader, MaskLoading} from "./components";
import { useView } from "./state/view/hooks";
import {useList} from "./state/list/hooks";
import GlobalUpdater from "./state/global/updater";
import ListsUpdater from "./state/list/updater";
import ViewUpdater from "./state/view/updater";
import Multicall from "./state/multicall/updater";
import Application from "./state/application/updater";
import TransactionUpdater from "./state/transactions/updater";
// import { axios } from './utils'
// import Gmodal from "./components/Modal";
function Updaters() {
  return (
    <>
      <GlobalUpdater />
      <ListsUpdater />
      <ViewUpdater />
      <Multicall />
      <Application />
      <TransactionUpdater />
    </>
  )
}

const App: React.FC = () => {
  const { isMobile } = useView();
  const {CurrentSwapLiquidity} = useList()
  // console.log('CurrentSwapLiquidity----')
  // console.log(CurrentSwapLiquidity)
  return (
    <Suspense fallback={null}>
      {/* <Gmodal /> */}
      <Web3ReactManager>
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/swap" exact>
            <Updaters />
            <div className="page-swap">
              <AppHeader />
              {CurrentSwapLiquidity
                ? (isMobile ? <MobileToken /> : <Token />)
                // ? (
                //     <div className='token-skeleton' style={{position: 'relative'}}>
                //       <MaskLoading />
                //     </div>
                //   )
                : (
                  <div className='token-skeleton' style={{position: 'relative'}}>
                    <MaskLoading />
                  </div>
                )}
              {/*{isMobile ? <MobileToken /> : <Token />}*/}
            </div>
          </Route>
        </Switch>
      </Web3ReactManager>
    </Suspense>
  );
};

export default App;
